import {debounce} from "lodash";
import Upload from "@/components/field/Upload";

export default {
    name: "MediaSelect",
    components: {
        'upload': Upload
    },
    props: {
        value: {},
        query: {
            default: () => {
                return {}
            }
        },
        multiple: {
            type: Boolean,
            default: false
        },
        column: {
            default: 'is-2'
        }
    },
    data() {
        let data = this.value;
        if (!data) {
            data = this.multiple ? [] : null
        }
        return {
            data: data,
            response: {
                count: 0,
                results: []
            },
            isShow: false,
            loading: false,
            q: {
                search: null,
                page_size: 6,
                oder_by: '-id',
                page: 1
            }
        }
    },
    methods: {
        fetch: debounce(async function (text) {
            this.loading = true;
            this.q.search = text;
            this.$api.media.list({
                ...this.q,
                ...this.query
            }).then((res) => {
                this.response = res;
            }).catch(() => {
                this.fetch(this.q.search);
            }).finally(() => {
                this.loading = false
            });
        }),
        uploaded(results) {
            this.response.results.splice(this.response.results.length - 1 - results.length, results.length)
            this.response.results = results.concat(this.response.results);
        },
        select(img) {
            if (this.multiple) {
                let index = this.data.map(x => x.id).indexOf(img.id);
                if (index >= 0) {
                    this.data.splice(index, 1);
                } else {
                    this.data.push(img);
                }
            } else {
                if (this.data && this.data.id === img.id) {
                    this.data = null;
                } else {
                    this.data = img;
                }
            }
        },
        showModal(flag = false) {
            if (!flag) {
                this.isShow = true;
                if (this.response.results.length === 0) {
                    this.fetch(null);
                }
            }
        },
        deleteItem(index) {
            if (this.multiple) {
                this.data.splice(index, 1);
            } else {
                this.data = null;
            }
        }
    },
    watch: {
        data: {
            deep: true,
            handler: function () {
                this.$emit('input', this.data);
            }
        },
        value: {
            deep: true,
            handler: function () {
                this.data = this.value;
            }
        },
        'q.page'() {
            this.fetch(this.q.search);
        },
        'q.search'() {
            this.q.page = 1;
        }
    },
    computed: {
        shows() {
            if (this.multiple) {
                return this.data;
            } else {
                return this.data ? [this.data] : []
            }
        }
    }
}
