export default {
    name: "FieldJson",
    props: {
        schema: {},
        value: {
            default: () => {
                return {}
            }
        }
    },
    data() {
        return {
            temp: this.value
        }
    },
    watch: {
        temp: {
            handler: function (after, before) {
                this.$emit('input', after)
            },
            deep: true
        }
    }
}
